@import "../mixins.scss";

$iconos: #F0DB4F, #2965f1, #E44D26;

//piano

section#seccion-1a.piano {
    @include t(100%,100vh);
    @include centrar(vh);
    position: relative;
    @include skills;
    overflow: hidden;
    h3 {
        position: absolute;
        top: 60px;
        font-size: 2em;
    }
    ul {
        border: 3px solid $piano1;
    }
    ul.no-active {
        li {
            color: $piano1;
            &::before {
                background: $piano1;
            }
        }
    }
    ul.active {
        li {
            color: $piano1;
        }
    }
}

//toxic

section#seccion-1a.toxic {
    @include centrar(vh);
    position: relative;
    @include skills;
    overflow: hidden;
    h3 {
        position: absolute;
        top: 60px;
        font-size: 2em;
        color: #fff;
        text-shadow: 0 0 10px $toxic2;
    }
    ul {
        cursor: pointer;
        background: rgba(100,100,100,0.5);
        backdrop-filter: blur(10px);
    }
    ul.no-active {
        border-radius: 10px;
        li {
            color: #fff;
            &::before {
                background: #fff;
                box-shadow: 0 0 15px $toxic2;
            }
        }
    }
    ul.active {
        border-radius: 20px;
        li {
            color: #ddd;
        }
    }
}
@media (orientation:landscape){
    section#seccion-1a.toxic {
        @include t(50%,100vh);
    }
}
@media (orientation:portrait){
    section#seccion-1a.toxic {
        @include t(100%,100vh);
    }
}